.pulse::after, .pulse::before{
  -webkit-animation: Pulse 6s ease infinite;
  animation: Pulse 6s ease infinite;
}

@-webkit-keyframes Pulse {
  0%{opacity: 0;}
  50%{opacity: 100;}
  100%{opacity: 0;}
}
@keyframes Pulse { 
  0%{opacity: 0;}
  50%{opacity: 100;}
  100%{opacity: 0;}
}

button,.button{
  transition: all 0.3s;
  background: rgba(41, 127, 185, 1);
  border-color: rgba(32, 32, 32, 0.5);
  color: white;
  font-family: 'Sleeve';
  font-size: 2em;
  letter-spacing: 0px;
  position: relative;
  overflow: hidden;
}

button:hover,.button:hover{
  border-color: orange;
  cursor: pointer;
}

button:after,.button:after {
  content: '';
  position: absolute;
  width: 150%;
  height: 150%;
  background: rgba(32, 32, 32, 1);
  -webkit-transform: skew(25deg) scale(0);
          transform: skew(25deg) scale(0);
  left: -25%;
  top: -25%;
  transition: 300ms ease-in-out;
}
button:hover:after,.button:hover:after {
  -webkit-transform: skew(25deg) scale(1);
          transform: skew(25deg) scale(1);
}
button > *,.button>*{
  position: relative;
  z-index: 1;
}


button:focus,.button:focus{
  outline: none;
}
  
