.navContainer{
  width: 350px;
  height: 80px;
  margin: auto 10px;
  flex: 0 1 auto;
  display: flex;
  justify-content: center;
}

.NavItem{
  transition: all 1s;
  text-align: center;
  flex: 0 0 auto;

}

.NavItem span{
  margin: auto 20px;
  position: relative;
  padding: 0 10px;
  font-weight: 800;
  color: #2980B9;
  transition: all 0.3s;
}
.NavItem span::before, .NavItem span::after{
  opacity: 0.5;
  width: 3px;
  height: 100%;
  content: '';
  flex: 0 1 auto;
  background: #2980B9;
  transition: all 0.2s;
  position: absolute;
  height: 75%;
}

.NavItem span:hover:before, .NavItem span:hover:after{
  opacity: 1;
  height: 3px;
  width: calc(100% - 6px);
  background: orange;
}
.NavItem span:hover{
  color: white;
}

.NavItem span:hover:before, .NavItem.active span:before{
  top: 5px;
}

.NavItem span:hover:after, .NavItem.active span:after{
  bottom: 2px;
}

.NavItem span::before{
  padding-right: 1px;
  left: 0px;
  top: 15%;
  height: 75%;
}

.NavItem span::after{
  padding-left: 1px;
  right: 6px;
  bottom: 10%;
}
.NavItem:hover{
  cursor: pointer;
}

.NavItem.active > span{
  color: white;
}

.NavItem.active > span::before, .NavItem.active > span::after{
  background: orange;
  opacity: 1;
  height: 3px;
  width: calc(100% - 6px);
}

